import anime from 'animejs/lib/anime.es.js';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { rectPath } from './utils/paths';
import { loadImage } from './utils/utils';

import texture from '../images/crayon.jpg';
// import texture from '../images/bg/004.png';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

function drawRect(rect) {
  ctx.beginPath();
  rect.forEach((p, idx) => {
    if (idx === 0) {
      ctx.moveTo(p.x, p.y)
    } else {
      ctx.lineTo(p.x, p.y);
    }
  });
  ctx.lineTo(rect[0].x, rect[0].y);
}

function drawLine(pos1, pos2) {
  ctx.beginPath();
  ctx.moveTo(pos1.x, pos1.y);
  ctx.lineTo(pos2.x, pos2.y);
}

const targ = {
  p1x: 1,
  p1y: 1,
  p2x: 2,
  p2y: 2,
  letter1: 0
};

anime({
  targets: targ,
  keyframes: [
    {
      p1x: 2,
      p1y: 1,
      p2x: 1,
      p2y: 1,
      letter1: 1
    },
    {
      p1x: 2,
      p1y: 2,
      p2x: 1,
      p2y: 1,
      letter1: 2
    },
    {
      p1x: 1,
      p1y: 2,
      p2x: 2,
      p2y: 2,
      letter1: 3
    },
    {
      p1x: 1,
      p1y: 1,
      p2x: 2,
      p2y: 2,
      letter1: 3
    }
  ],
  loop: true,
  duration: 1000,
  easing: 'easeOutQuint',
})

const letters = ['A', 'B', 'C', 'D'];

function textInCenter(txt, tl, wid, hei) {
  const fontSize = 80;

  ctx.font = `bold ${fontSize}px Helvetica`;

  let txtWid = ctx.measureText(txt);
  ctx.fillText(txt,
    tl.x + ((wid - txtWid.width) / 2),
    tl.y + ((hei / 2) + (fontSize * 0.4))
  );
}

let img;

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  ctx.save();

  const tileSize = width / 4;
  const size = (mod) => tileSize * mod;

  ctx.lineWidth = 2;

  const rect = rectPath(size(targ.p1x), size(targ.p1y), size(1), size(1));
  const rect2 = rectPath(size(targ.p2x), size(targ.p2y), size(1), size(1));

  drawRect(rect);
  ctx.fill();

  drawRect(rect2);
  ctx.stroke();

  const [p11, p12, p13, p14] = rect;
  const [p21, p22, p23, p24] = rect2;
  drawLine(p11, p21);
  ctx.stroke();

  drawLine(p12, p22);
  ctx.stroke();

  drawLine(p13, p23);
  ctx.stroke();

  drawLine(p14, p24);
  ctx.stroke();

  ctx.fillStyle = 'black';
  textInCenter('&', {
    x: size(targ.p2x),
    y: size(targ.p2y)
  }, size(1), size(1));

  ctx.globalCompositeOperation = 'source-atop';

  // ctx.drawImage(img, width * 0.2, height * 0.2, width * 0.6, height * 0.6);
  ctx.drawImage(img, width * -0.2, height * -0.2, width * 1.4, height * 1.4);


  ctx.fillStyle = 'white';
  textInCenter(letters[Math.floor(targ.letter1)], {
    x: size(targ.p1x),
    y: size(targ.p1y)
  }, size(1), size(1));

  ctx.restore();
});

loadImage(texture)
  .then(({ img: _img }) => {
    img = _img;
    app.start();
  });
